<template>
    <div class="Form chọn khu xưởng" v-if="isShowSelectFactoryZone">
        <el-row :gutter="24" justify="start">
            <el-col :md="8">
                <el-form-item :label="$t('Khu xưởng/Phòng y tế')">
                    <el-select v-model="frm.FactoryZone" :placeholder="$t('Vui lòng chọn khu xưởng/Phòng y tế')" clearable>
                        <el-option v-for="item in FactoryZoneLs" :key="item.value" :label="item.label"
                            :value="item.value" />
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row :gutter="24" justify="start">
            <el-col :md="8">
                <el-button size="large" type="success" @click="NextFromSelectFactory()" plain>
                    <el-icon>
                        <Right />
                    </el-icon>
                    &nbsp;{{ $t('Tiếp theo') }}</el-button>
            </el-col>
        </el-row>
    </div>
    <div v-else class="Form thao tac don xin cap phat">
        <div style="text-align:left" v-if="this.frm.receiveDocNum?false:true">
            <el-button style="border:none" @click="ResetSome()">
                <el-icon>
                    <Back />
                </el-icon>
                &nbsp; {{ $t('Trở lại') }}
            </el-button>
        </div>
        <h3 class="title">{{ $t('Đơn xin cấp phát') }}:</h3>
        <el-divider />
        <div class="Form thao tác nhập mã hiệu thuốc và add tạm thời">
            <el-row :gutter="24" justify="start">
                <el-col :md="6">
                    <el-form-item :label="$t('Đầu đơn xin cấp phát')" :rules="[{ required: false },]">
                        <el-input v-model="frm.receiveDocNum" readonly />
                    </el-form-item>
                </el-col>
                <el-col :md="6">
                    <el-form-item :label="$t('Đơn vị xin cấp phát')" :rules="[{ required: true },]">
                        <el-input v-model="frm.receiveUnit" clearable :readonly="this.frm.receiveDocNum?true:false" />
                    </el-form-item>
                </el-col>
                <el-col :md="6">
                    <el-form-item :label="$t('Khu xưởng/Phòng y tế')" :rules="[{ required: false },]">
                        <el-input v-model="frm.FactoryZone" readonly />
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="24" justify="start" v-if="isFirstStationNow">
                <el-col :md="6">
                    <el-form-item :label="$t('Số hiệu')" :rules="[{ required: true },]">
                        <el-input v-model="curMedicineID" clearable @change="ChangeMedicineId()" />
                    </el-form-item>
                </el-col>
                <el-col :md="6">
                    <el-form-item :label="$t('Tên hàng')" :rules="[{ required: false },]">
                        <el-input v-model="curMedicineName" readonly />
                    </el-form-item>
                </el-col>
                <el-col :md="6">
                    <el-form-item :label="$t('Quy cách')" :rules="[{ required: false },]">
                        <el-input v-model="curMedicineObjUnit" readonly />
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="24" justify="start" v-if="isFirstStationNow">
                <el-col :md="6">
                    <el-form-item :label="$t('Số lượng tồn')" :rules="[{ required: false },]">
                        <el-input v-model="curQtyInStockNow" readonly />
                    </el-form-item>
                </el-col>

                <el-col :md="6">
                    <el-form-item :label="$t('Số lượng xin cấp')" :rules="[{ required: true },]">
                        <el-input-number v-model="curQtyRequest" :placeholder="$t('Nhập vào số lượng')" clearable :min="1"
                            :max="1000000000" :precision="0" value-on-clear="min" />
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="24" v-if="isFirstStationNow">
                <el-col :md="11">
                </el-col>
                <el-col :md="2">
                    <el-button size="large" type="success" @click="AddTempMedicineExport()" plain>
                        <el-icon>
                            <Plus />
                        </el-icon>
                        &nbsp;{{ $t('Thêm') }}</el-button>
                </el-col>
                <el-col :md="11">
                </el-col>
            </el-row>
        </div>
        <div class="Form hien thi thong tin thuoc sau khi nhan nut Add"
            v-if="this.frm.medicineExportLs != null && this.frm.medicineExportLs.length > 0">
            <h3 style="text-align: left;">{{ $t('Chi tiết các sản phẩm xin:') }}</h3>
            <el-divider />
            <el-table :data="this.frm.medicineExportLs" style="width: 100%">
                <el-table-column type="index" :label="$t('Số thứ tự')"  width="100"/>
                <el-table-column prop="F_MEDICINE_ID" :label="$t('Số hiệu')" />
                <el-table-column prop="F_MEDICINE_NAME" :label="$t('Tên hàng')" />
                <el-table-column prop="F_MEDICINE_OBJ_UNIT" :label="$t('Quy cách')" />
                <el-table-column prop="F_QTY_REQUEST" :label="$t('Số lượng xin cấp')" />
                <el-table-column prop="F_REQUEST_DATE" :label="$t('Ngày xin cấp')" />
                <el-table-column prop="F_QTY_RECEIVED" :label="$t('Số lượng xuất')" />
                <el-table-column prop="F_RECEIVED_DATE" :label="$t('Ngày nhận')" />
                <el-table-column prop="F_RECEIVED_EMP" :label="$t('Người nhận')" />
                <el-table-column :label="$t('Hành động')" v-if="isFirstStationNow">
                    <template #default="scope">
                        <el-button size="small" type="danger" plain
                            @click="DeleteTempMedicineExport(scope.row.F_MEDICINE_ID)">
                            <el-icon>
                                <Delete />
                            </el-icon>
                            &nbsp; {{ $t('Xóa') }}
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="Form chọn lưu trình ký"   v-if="this.frm.signStationLs!=null && this.frm.signStationLs.length>0">
            <h3 style="text-align: left;">{{ $t('Lưu trình ký:') }}</h3>
            <el-divider />
            <el-table :data="this.frm.signStationLs" style="width: 100%;height:100%;">
                <el-table-column type="index" :label="$t('Số thứ tự')"  width="100"/>
                <el-table-column prop="F_SIGN_STATION" :label="$t('Tên Trạm ký')" />
                <el-table-column prop="F_SIGN_EMP" :label="$t('Mã thẻ')" />
                <el-table-column prop="F_SIGN_NAME" :label="$t('Họ tên')" />
                <el-table-column prop="F_SIGN_MAIL" :label="$t('Email')" />
            </el-table>
        </div>
        <div class="Form lich su ky duyet" v-if="this.RSignDetail!=null && this.RSignDetail.length>0">
            <h3 style="text-align: left;">{{ $t('Lịch sử ký duyệt:') }}</h3>
            <el-divider />
            <el-table :data="this.RSignDetail" style="width: 100%;height:100%;">
                <el-table-column type="index" :label="$t('Số thứ tự')"  width="100"/>
                <el-table-column prop="STATION_NAME" :label="$t('Tên Trạm ký')" />
                <el-table-column prop="SIGN_STATUS" :label="$t('Trạng thái')" />
                <el-table-column prop="SIGN_EMP" :label="$t('Mã thẻ người ký')" />
                <el-table-column prop="SIGN_NAME" :label="$t('Họ tên người ký')" />
                <el-table-column prop="SIGN_MAIL" :label="$t('Email người ký')" />
                <el-table-column prop="SIGN_NOTES" :label="$t('Ý kiến')" />
                <el-table-column prop="CREATE_DATE" :label="$t('Ngày ký')" />
            </el-table>
        </div>

        <div class="Form thao tac ky duyet" style="margin-top:40px">
            <el-row :gutter="24">
                <el-col :md="10">
                </el-col>
                <el-col :md="4">
                    <el-button size="large" type="success" @click="DoSign()" plain>
                        <el-icon>
                            <EditPen />
                        </el-icon>
                        &nbsp;{{ $t('Ký duyệt') }}</el-button>
                    &nbsp;
                    <el-button size="large" type="danger" @click="DoReject()" plain>
                        <el-icon>
                            <Failed />
                        </el-icon>
                        &nbsp;{{ $t('Từ chối') }}</el-button>
                </el-col>
                <el-col :md="10">
                </el-col>
            </el-row>

        </div>
    </div>
    <div class="Dialog">
        <el-dialog class="Form xác nhận ký duyệt" v-model="isShowSignDialog" :title="$t('Xác nhận ')+ (frm.isReject==true?$t('từ chối'):$t('ký duyệt')) " width="500">
            <el-form >
                <el-form-item :label="$t('Ý kiến')">
                    <el-input v-model="frm.remark"
                    :placeholder="$t('Nhập vào ý kiến của bạn')"
                    type="textarea" />
                </el-form-item>
                <el-form-item :label="$t('Trả về trạm:')" v-if="frm.isReject">
                    <el-radio-group v-model="frm.RejectStation">
                        <el-radio label="FirstStation">{{ $t('Người làm đơn') }}</el-radio>
                        <el-radio label="BeforeStation">{{ $t('Trạm ký trước') }}</el-radio>
                   </el-radio-group>
                </el-form-item>

            </el-form>
            <template #footer>
                <div class="dialog-footer">
                    <el-button plain type="primary"  @click="DoSignApplicationForIssuance()">{{ $t('Gửi đi') }}</el-button>
                    <el-button plain type="warning" @click="isShowSignDialog = false">
                        {{$t('Hủy bỏ')}}
                    </el-button>
                </div>
            </template>
        </el-dialog>

    </div>
</template>
<script>
import { ElMessage } from 'element-plus'
import api from "@/model/api.js"
export default
    {
        name: "NewApplicationForIssuance",
        data() {
            return {
                isFirstStationNow:false, //tram hien tai dang can ky la tram dau tien cua luu trinh or dang tao don moi
                isShowSignDialog:false,
                FactoryZoneLs: [],
                isShowSelectFactoryZone: true,
                RSignDetail:[],// lich su ky duyet
                frm: {
                    receiveDocNum: "", // ma dau don xin
                    medicineExportLs: [], //danh sach cac loai thuoc xin cap phat sau khi nhan nut add
                    receiveUnit: "", // ten don vi/ bo phan  xin cap phat thuoc
                    FactoryZone: "",// khu xuong hien tai
                    signStationLs: [],// list cac tram ky cua luu trinh : gom emp,mail, vv
                    remark:"",// y kien truoc khi nhan ky or reject
                    RejectStation:"BeforeStation", // Reject den tram nao : FirstStation: tram dau luu trinh ky , BeforeStation: tram truoc do
                    isReject:false,  // co phai la reject 
                },
                // thong tin tam thoi khi user truoc khi nhan nut add
                curMedicineID: "",// so hieu thuoc
                curMedicineName: "",  // ten thuoc
                curMedicineNameVi: "",  // ten thuoc tieng viet
                curMedicineNameEn: "",  // ten thuoc tieng anh
                curMedicineObjUnit: "", // quy cach thuoc
                curMedicineObjUnitVi: "", // quy cach thuoc tieng viet
                curMedicineObjUnitEn: "", // quy cach thuoc tieng anh
                curQtyInStockNow: 0,// so luong ton kho hien tai
                curQtyRequest: 0, //so luong xin cap
                //

            }
        },
        methods: {
            GetHistorySignOfApplicationForIssuance()
            {
                this.RSignDetail=[]
                api.GetHistorySignOfApplicationForIssuance(
                    {
                        receiveDocNum:this.frm.receiveDocNum
                    },
                (dat)=>{
                    if (dat.data.status == "error")
                    {
                        ElMessage.error(dat?.data?.message)
                    }
                    else
                    {
                        if(dat.data.data.length>0)
                        {
                            this.RSignDetail  =dat.data.data
                        }
                       
                    }
                
                },
                (er)=>{
                    ElMessage.error(er.message) 
                })
            }
            ,
            DoSignApplicationForIssuance()   // su kien ky duyet/reject don xin cap phat 
            {
                api.DoSignApplicationForIssuance(
                  this.frm,
                (dat)=>{
                    if (dat.data.status == "error")
                    {
                        ElMessage.error(dat?.data?.message)
                    }
                    else  //success
                    {
                        this.frm.receiveDocNum =  dat.data.data;
                          ElMessage({
                                message: dat.data.message,
                                type: 'success',
                             })


                    }
                },
                (er)=>{
                   ElMessage.error(er.message)
                })
            },
            DoSign()
            {
                this.frm.isReject=false;
                this.isShowSignDialog=true;
            },
            DoReject(){
                this.frm.isReject=true;
                this.isShowSignDialog=true;
            },
            GetSignRoutingOfApplicationForIssuance() {
                this.frm.signStationLs = [];
                api.GetSignRoutingOfApplicationForIssuance(
                    (dat) => {
                        if (dat.data.status == "error") {
                            ElMessage.error(dat?.data?.message)
                        }
                        else  //success
                        {
                            this.frm.signStationLs = dat.data?.data;
                        }
                    },
                    (er) => {
                        ElMessage.error(er.message)
                    }
                )
            },
            DeleteTempMedicineExport(curMedinceId) {
                let index = this.frm.medicineExportLs.findIndex(item => item.F_MEDICINE_ID === curMedinceId);
                if (index != -1) {
                    this.frm.medicineExportLs.splice(index, 1)
                }
            },
            CheckValidCurObj() {
                if (!this.curMedicineID.trim()) {

                    ElMessage.error(this.$t("Vui lòng chọn số hiệu"))
                    return false;
                }
                if (!this.frm.receiveUnit.trim()) {
                    ElMessage.error(this.$t("Vui lòng nhập vào tên đơn vị xin cấp phát"))
                    return false;
                }

                if (!this.curMedicineName.trim()) {
                    ElMessage.error(this.$t("Không có thông tin tên thuốc"))
                    return false;
                }
                if (!this.curMedicineObjUnit.trim()) {
                    ElMessage.error(this.$t("Không có thông tin quy cách"))
                    return false;
                }
                if (this.curQtyRequest <= 0) {
                    ElMessage.error(this.$t("Số lượng xin cấp phát phải >0"))
                    return false;
                }

            },
            AddTempMedicineExport() {
                if (this.CheckValidCurObj() == false)
                    return

                let tempCurMedicine = {
                    F_MEDICINE_ID: this.curMedicineID,
                    F_MEDICINE_NAME: this.curMedicineNameVi + "/" + this.curMedicineNameEn,
                    F_MEDICINE_NAME_VI: this.curMedicineNameVi,
                    F_MEDICINE_NAME_EN: this.curMedicineNameEn,
                    F_MEDICINE_OBJ_UNIT: this.curMedicineObjUnitVi + "/" + this.curMedicineObjUnitEn,
                    F_MEDICINE_OBJ_UNIT_VI: this.curMedicineObjUnitVi,
                    F_MEDICINE_OBJ_UNIT_EN: this.curMedicineObjUnitEn,
                    F_QTY_REQUEST: this.curQtyRequest,
                    F_REQUEST_DATE: "",
                    F_QTY_RECEIVED: 0,
                    F_RECEIVE_DOC_NUM: this.frm.receiveDocNum,
                    F_FACTORYZONE: this.frm.FactoryZone,
                    F_RECEIVED_DATE :"",
                    F_RECEIVED_EMP:"",

                }
                if (this.frm.medicineExportLs.length > 0) {
                    let oldMedicinelS = this.frm.medicineExportLs.filter((item) => { if (item.F_MEDICINE_ID == tempCurMedicine.F_MEDICINE_ID) return true; });
                    if (oldMedicinelS.length > 0) {
                        ElMessage.error(this.$t("Số hiệu này đã được thêm, vui lòng chọn số hiệu khác"))
                        return
                    }
                }
                this.frm.medicineExportLs.push(JSON.parse(JSON.stringify(tempCurMedicine)))
                this.ResetSomeCurInfor();
                this.ResetcurMedinceId();
            },
            ChangeMedicineId() {
                if (this.curMedicineID.trim() == "") {
                    this.ResetSomeCurInfor();
                    ElMessage.error(this.$t("Vui lòng nhập vào số hiệu"))
                    return
                }
                this.ResetSomeCurInfor();
                api.CheckMedicineIDForApplicationForIssuance(
                    {
                        MedicineID: this.curMedicineID.trim(),
                        FactoryZone: this.frm.FactoryZone
                    },
                    (dat) => {
                        if (dat.data.status == "error") {
                            ElMessage.error(dat?.data?.message)
                        }
                        else  //success
                        {
                            if (dat?.data?.data) {
                                let temp = dat?.data?.data;
                                this.curMedicineNameVi = temp.MedicineNameVi;
                                this.curMedicineNameEn = temp.MedicineNameEn;
                                this.curMedicineName = this.curMedicineNameVi + "/" + this.curMedicineNameEn;

                                this.curMedicineObjUnitVi = temp.MedicineObjUnitVi;
                                this.curMedicineObjUnitEn = temp.MedicineObjUnitEn;
                                this.curMedicineObjUnit = this.curMedicineObjUnitVi + "/" + this.curMedicineObjUnitEn;

                                this.curQtyInStockNow = temp.QtyInStockNow;

                            }
                        }
                    },
                    (er) => {
                        ElMessage.error(er.message)
                    })

            },
            ResetSome() {
                this.frm.FactoryZone = "";
                this.isShowSelectFactoryZone = true;
                this.curMedicineID = "";
                this.ResetSomeCurInfor();
            },
            ResetcurMedinceId() {
                this.curMedicineID = "";
            },
            ResetSomeCurInfor() {
                this.curMedicineName = ""  // ten thuoc
                this.curMedicineNameVi = ""  // ten thuoc tieng viet
                this.curMedicineNameEn = "" // ten thuoc tieng anh
                this.curMedicineObjUnit = ""// quy cach thuoc
                this.curMedicineObjUnitVi = ""// quy cach thuoc tieng viet
                this.curMedicineObjUnitEn = "" // quy cach thuoc tieng anh
                this.curQtyInStockNow = 0// so luong ton kho hien tai
                this.curQtyRequest = 0 //so luong xin cap
            },
            NextFromSelectFactory() {
                if (!this.frm.FactoryZone) {
                    ElMessage.error(this.$t("Vui lòng chọn nhà xưởng đầu tiên"))
                    return
                }
                else
                {
                    this.isShowSelectFactoryZone = false;
                    this.GetSignRoutingOfApplicationForIssuance();
                }
                   
            },
            GetFactoryZone() {
                this.FactoryZoneLs = [];
                api.GetFactoryZone((dat) => {
                    if (dat.data.data) {
                        dat.data.data.forEach((item) => {
                            this.FactoryZoneLs.push({ label: item.F_DATA2, value: item.F_DATA2 })
                        })

                    }

                }, (er) => {
                    ElMessage.error(er.message)
                });
            },
            GetTotalInforOfAnApplicationForIssuance()
            {
                api.GetTotalInforOfAnApplicationForIssuance(
                    {receiveDocNum:this.frm.receiveDocNum},
                (dat)=>{
                    if (dat.data.status == "error") {
                            ElMessage.error(dat?.data?.message)
                        }
                        else  //success
                        {
                            if(dat?.data?.data==null)
                            {
                                ElMessage.error(this.$t("Không có dữ liệu đơn trên hệ thống"))
                            }
                            else
                            {
                                let temp =   dat?.data?.data;
                                this.frm.receiveDocNum=temp.receiveDocNum
                                this.frm.receiveUnit=temp.receiveUnit
                                this.frm.FactoryZone=temp.FactoryZone
                                this.frm.medicineExportLs=temp.medicineExportLs
                                this.isFirstStationNow=temp.isFirstStation
                                this.frm.signStationLs=temp.signStationLs
                            }
                          
                        }
                },
                (er)=>{
                   ElMessage.error(er.message)
                })
            },
        },
        mounted() {
           
        },
        created()
        {
            this.GetFactoryZone();
            //khi user click vao duong link don doi ky-> se nhay zo day
            if(this.$route.query.receiveDocNum) // don da co-> xem view va ky don
            {
                this.isShowSelectFactoryZone=false;
                this.frm.receiveDocNum= this.$route.query.receiveDocNum?.trim()
                this.GetHistorySignOfApplicationForIssuance();
                this.GetTotalInforOfAnApplicationForIssuance()
            }
            else
            {   // tao don moi 
                this.isFirstStationNow=true;
                this.isShowSelectFactoryZone=true;
            }
        },
    }
</script>
<style scoped>
table,
th,
td {
    padding: 10px;
    border: 1px solid black;
    border-collapse: collapse;
}

.title {
    text-align: left;
    padding-left: 5px;
}
</style>